import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import systemModule from '@/views/system';

let routerInit = () => new Router({
    routes: [
        {
            path: '/',
            redirect: '/system'
        },
        {
            path: '/system',
            name: 'system',
            redirect: '/system/systemManage',
            component: systemModule.system,
            children: [
                {
                    path: 'systemManage',
                    name: 'systemManage',
                    component: systemModule.systemManage
                }
            ]
        }
    ]
  });
  
  let router = routerInit()

  export default router




